.center_align_col {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.center_align_row {
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: center;
}

.padding10px {
  padding: 10px;
}

.login_container {
  padding: 20px;
  box-shadow: 5px 1px 20px 5px #f6f7f8;
}

/* log in with ~~~ */

.login_with_btn {
  background-color: #f6f7f8;
  border: 1px solid white;
  border-radius: 5px;
  padding: 10px 20px 10px 20px;
  color: #4e5d78;
  font-weight: 600;
  margin: 10px;
  height: 40px;
  &:hover {
    cursor: pointer;
    background-color: white;
    color: #377dff;
    border: 1px solid #377dff;
  }
}

/* https://nuggy875.tistory.com/115 */

.big_icon {
  width: 15px;
  height: 15px;
  display: inline-block;
  margin-right: 10px;
  vertical-align: -25%;
}

.line_width {
  width: 180px;
}

.bold_gray_text {
  font-weight: bold;
  color: #4e5d78;
}

.bold_blue_text {
  font-weight: bold;
  color: #377dff;
}

.gray {
  color: #4e5d78;
}

.blue {
  color: #377dff;
}

/* 입력창 */

.userInputs {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  align-items: flex-start;
  gap: 10px;
  padding: 20px;

  .inputbox {
    border: solid 1px #eaebec;
    border-radius: 5px;
    padding: 10px 15px 10px 20px;
    width: 320px;
    display: flex;
    &:focus-within {
      outline: solid 1px #377dff;
    } /* outline 테두리 없애기 */

    .input {
      border: none;
      flex: 1;
      &:focus {
        outline: none;
      } /* outline 테두리 없애기 */
    }
  }
}

.checkbox {
  width: 360px;
  display: flex;
  padding-top: 15px;
}

.flex1 {
  flex: 1;
}

/* 로그인 버튼 */
.login_btn {
  background-color: #377dff;
  color: white;
  border: none;
  border-radius: 5px;
  padding: 10px 20px 10px 20px;
  margin: 10px;
  width: 90%;
  height: 40px;

  &:hover {
    cursor: pointer;
    background-color: white;
    color: #377dff;
    border: 1px solid #377dff;
  }
}

.pointer:hover {
  cursor: pointer;
  font-weight: bold;
}
