@import "./responsive"; // 반응형 mixin import

table {
  width: 100%;
  border-top: 1px solid #444444;
  border-collapse: collapse;

  th {
    border-bottom: 2px solid #444444;
    padding: 10px;
    background-color: #f6f7f8;
    &:hover {
      cursor: pointer;
      color: #377dff;
    }
  }

  td {
    border-bottom: 1px solid #444444;
    padding: 10px;
  }
}

.WriteTitle {
  width: 98%;
  font-size: large;
  margin-bottom: 5px;
  border-radius: 3px;
  padding: 5px;

  &:focus {
    border-color: #377dff;
    outline: none;
  }
}

.WriteContent {
  width: 98%;
  height: 50vh;
  font-size: large;
  margin-bottom: 5px;
  resize: none;
  border-radius: 3px;
  padding: 5px;

  &:focus {
    border-color: #377dff;
    outline: none;
  }
}

.pointer:hover {
  cursor: pointer;
  font-weight: bold;
}

.open_btn {
  margin: 5px;
  background-color: #8891a1;
  border: 1px solid white;
  border-radius: 5px;
  padding: 9px;
  color: white;
  &:hover {
    cursor: pointer;
    background-color: white;
    color: #377dff;
    border: 1px solid #377dff;
  }
}

