
.brandIcon {
    width: 25px;
    height: 25px;
    display:inline-block;
    margin-right: 5px; 
    vertical-align: -25%;
    color: #377dFF;
  }
  
  .bar_btn {
    background-color: white;
    border: 1px solid #4E5D78;
    border-radius: 5px;
    padding: 5px 10px 5px 10px;
    color: #4E5D78;
    font-weight: 600;
    box-shadow: 2px 2px 5px 1px #dedfe0;
    &:hover {
        cursor: pointer;
        background-color: white;
        color: #377dFF;
        border: 1px solid #377dFF;
    }
    
  }
  
  
  .bar_user_id {
    color: #377dFF;
    margin-right: 10px;
  }
  
  