.center_align_col {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.center_align_row {
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: center;
}

.big_icon {
  width: 15px;
  height: 15px;
  display: inline-block;
  margin-right: 10px;
  vertical-align: -25%;
}

.pointer:hover {
  cursor: pointer;
  font-weight: bold;
}

.red {
  color: red;
}

.gray {
  color: #4e5d78;
}

.blue {
  color: #377dff;
}

.bold_gray_text {
  font-weight: bold;
  color: #4e5d78;
}

.bold_blue_text {
  font-weight: bold;
  color: #377dff;
}

/* 모달 */
.bg {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);

  display: flex;
  justify-content: center;
  align-items: center;

  .modalBox {
    background-color: #fff;
    width: 600px;
    height: auto;
    padding: 15px;
    text-align: right;
    border-radius: 10px;

    button {
      margin-bottom: 15px;
    }
  }
}

/* 버튼 */

.open_btn {
  margin: 5px;
  background-color: #8891a1;
  border: 1px solid white;
  border-radius: 5px;
  padding: 9px;
  color: white;
  &:hover {
    cursor: pointer;
    background-color: white;
    color: #377dff;
    border: 1px solid #377dff;
  }
}

.signup_container {
  padding: 20px;
  box-shadow: 5px 1px 20px 5px #f6f7f8;
}

/* 인풋박스 */

.signup_inputbox {
  border: solid 1px #eaebec;
  border-radius: 5px;
  padding: 10px 15px 10px 20px;
  width: 350px;
  display: flex;
  margin-top: 10px;
  margin-bottom: 10px;
  &:focus-within {
    outline: solid 1px #377dff;
  } /* outline 테두리 없애기 */

  .signup_input {
    border: none;
    flex: 1;
    &:focus {
      outline: none;
    } /* outline 테두리 없애기 */
  }
}

.category {
  margin: 20px;

  .inputZipCode {
    border: solid 1px #eaebec;
    border-radius: 5px;
    padding: 10px 15px 10px 20px;
    margin-top: 10px;
    width: 80px;
    &:focus-within {
      outline: solid 1px #377dff;
    } /* outline 테두리 없애기 */
  }

  .inputAddress {
    border: solid 1px #eaebec;
    border-radius: 5px;
    padding: 10px 15px 10px 20px;
    width: 350px;
    margin-top: 5px;
    &:focus-within {
      outline: solid 1px #377dff;
    } /* outline 테두리 없애기 */
  }
}

/* 회원가입 버튼 */
.signup_btn {
  background-color: #377dff;
  color: white;
  border: none;
  border-radius: 5px;
  padding: 10px 20px 10px 20px;
  margin: 10px;
  width: 95%;
  height: 40px;

  &:hover {
    cursor: pointer;
    background-color: white;
    color: #377dff;
    border: 1px solid #377dff;
  }
}

.margin_vertical {
  margin-top: 10px;
  margin-bottom: 10px;
}

.chkText {
  font-size: small;
  padding-left: 10px;
}
